.advantages {
  padding: 0;
  position: relative;
}

.advantages__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 49px;
  padding-top: 24px;
  position: relative;

  @media (min-width: @mobile-width) and (max-width: @tablet-width) {
    br {
      display: none;
    }
  }

  @media (min-width: @tablet-width) {
    font-size: 21px;
    padding: 0 20px;
    margin-bottom: 56px;
  }

  @media (min-width: @desktop-width) {
    padding: 0 50px;
    margin-bottom: 58px;
  }
}

.advantages__description {
  margin: 0;

  @media (max-width: @mobile-width) {
    br {
      display: none;
    }
  }

  @media (min-width: @tablet-width) {
    padding: 0;
  }

  @media (min-width: @desktop-width) {
    br {
      display: none;
    }
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
  counter-reset: number 0;
  position: relative;

  @media (max-width: @mobile-width) {
    &::before {
      content: url("../img/triangle-section.svg");
      position: absolute;
      top: -14px;
    }
  }

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(4, auto);
  }
}


.advantages__item-img,
.advantages__item {
  counter-increment: number 1;

  .advantages__description {
    position: relative;
  }

  .advantages__description::before {
    position: absolute;
    top: -38px;
    left: 50%;
    transform: translateX(-50%);
    content: "— №" counter(number) " —";

    @media (min-width: @tablet-width) {
      top: -44px;
    }
  }
}

.advantages__item-img {
  @media (min-width: @mobile-width) and (max-width: @mobile-width-only) {
    &:first-child {
      .advantages__item-photo {
        padding: 30px 20px 40px;
      }
    }
  }

  @media (min-width: @tablet-width) {
    display: grid;
    padding: 0;
    grid-column: 1/-1;
    grid-template-columns: repeat(3, 1fr);

    & picture {
      grid-column: 2/-1;
    }

    &--reverse picture {
      grid-column: 1/span 2;
      grid-row: 1/2;
    }
  }

  @media (min-width: @desktop-width) {
    &:nth-child(2) {
      .advantages__description {
        padding: 0 24px;
      }
    }
  }
}

.advantages__item-photo--blue {
  color: #ffffff;
  background-color: @main-color;
  padding: 52px 33px 3px;
}

.advantages__item {
  text-align: center;
  background-color: #eeeeee;
  padding: 0 36px 25px;

  &:last-child {
    .advantages__description {
      padding-bottom: 4px;
    }
  }

  @media (min-width: @mobile-width) and (max-width: @mobile-width-only) {
    & + & {
      .advantages__title {
        border-top: 1px solid #d6d6d6;
      }
    }
  }

  @media (min-width: @tablet-width) {
    padding: 44px 32px 41px;
  }

  @media (min-width: @desktop-width) {
    padding: 52px 32px 48px;

    .advantages__title {
      padding: 0 90px;
    }

    .advantages__description {
      padding: 0 10px;
    }

    &:nth-child(4) {
      .advantages__description {
        padding: 0 34px;
      }
    }
  }
}

.advantages__item-wrapper {
  padding: 24px;
}

.advantages__item-photo {
  padding: 13px 18px 37px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-width: @tablet-width) {
    padding: 52px 33px 31px;
  }

  @media (min-width: @desktop-width) {
    padding: 52px 60px 51px;
  }
}

.advantages__image {
  @media (max-width: @mobile-width-only) {
    display: block;
    width: 100%;
  }

  @media (min-width: @tablet-width) {
    grid-column: 2/-1;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

.advantages__item-photo--reverse {
  @media (min-width: @tablet-width) {
    grid-column: 3/-1;
  }
}

.advantages__image--reverse {
  @media (min-width: @tablet-width) {
    grid-column: 1/span 2;
    grid-row: 1/2;
  }
}

.advantages__service {
  @media (min-width: @tablet-width) {
    display: flex;
    grid-column: 1 / 4;
  }
}
