@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  text-transform: uppercase;
  height: 100%;
  margin: 0;
  background-color: #ffffff;
}
a {
  text-decoration: none;
  color: #000000;
}
img {
  max-width: 100%;
  height: auto;
  display: block;
}
input,
button,
textarea {
  font-family: inherit;
}
.section-page {
  display: block;
  text-align: center;
  padding: 32px 20px;
}
@media (min-width: 768px) {
  .section-page {
    padding: 48px 124px 55px;
  }
}
@media (min-width: 1200px) {
  .section-page {
    padding: 60px 358px 44px;
  }
}
.section-page__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 36px;
  color: #000000;
  padding: 0 10px;
}
@media (min-width: 768px) {
  .section-page__title {
    font-size: 21px;
    margin-bottom: 30px;
  }
}
@media (min-width: 768px) {
  .section-page__title--large {
    font-size: 30px;
  }
}
.section-page__description {
  margin: 0;
}
.button {
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  background-color: #81b3d2;
  color: #ffffff;
  border: 0;
  text-transform: uppercase;
  display: inline-block;
  text-align: center;
  padding: 16px;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
  color: #ffffff30;
}
.button:active span {
  opacity: 0.3;
}
.button:disabled {
  background-color: #e5e5e5;
}
.main-header {
  min-height: 56px;
}
@media (max-width: 767px) {
  .main-header {
    display: flex;
    flex-direction: column;
  }
}
@media (min-width: 768px) {
  .main-header {
    position: relative;
    display: flex;
  }
}
.main-header__logo-image {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: -40px;
  position: relative;
}
@media (min-width: 768px) {
  .main-header__logo-image {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}
@media (max-width: 767px) {
  .main-nav:not(.main-nav--js) {
    order: -1;
  }
}
@media (min-width: 768px) {
  .main-nav {
    width: 100%;
  }
}
@media (max-width: 767px) {
  .main-nav__toggle {
    display: none;
  }
  .main-nav--js .main-nav__toggle {
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 64px;
    height: 56px;
    padding: 0;
    border: none;
    background: transparent url("../img/button-toggle-closed.svg") center / 23px 22px no-repeat;
  }
  .main-nav--js .main-nav__toggle:hover {
    opacity: 0.6;
  }
  .main-nav--js .main-nav__toggle:active {
    opacity: 0.3;
  }
  .main-nav--js.main-nav--opened .main-nav__toggle {
    background-image: url("../img/button-toggle-opened.svg");
    z-index: 1;
  }
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
  width: 100%;
}
@media (max-width: 767px) {
  .main-nav__list {
    position: relative;
    background-color: #81b3d2;
  }
  .main-nav__list::after {
    content: "";
    position: absolute;
    height: 7px;
    bottom: 0;
    right: 0;
    left: 0;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
    pointer-events: none;
  }
  .main-nav--js .main-nav__list {
    position: absolute;
    top: 0;
    left: 0;
    display: none;
  }
  .main-nav--js.main-nav--opened .main-nav__list {
    display: block;
  }
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
}
@media (max-width: 767px) {
  .main-nav__item:not(:last-child) {
    border-bottom: 1px solid rgba(1, 1, 1, 0.2);
  }
  .main-nav__item + .main-nav__item {
    border-top: 1px solid rgba(255, 255, 255, 0.2);
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    flex: 0 0 20%;
  }
  .main-nav__item:nth-child(4n+3) {
    margin-left: 20%;
  }
}
.main-nav__link {
  display: block;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #ffffff;
  padding: 17px 64px 11px;
}
@media (max-width: 767px) {
  .main-nav__link:hover {
    background-color: #669ec0;
  }
  .main-nav__link:active {
    background-color: #5496bd;
    color: #ffffff30;
  }
}
@media (min-width: 768px) {
  .main-nav__link {
    font-weight: 700;
    background-color: #ffffff;
    color: #000000;
    font-size: 14px;
    line-height: 21px;
    padding: 18px 20px 17px;
  }
  .main-nav__link:hover {
    color: #81b3d2;
  }
  .main-nav__link:active {
    opacity: 0.3;
  }
  .main-nav__link--active {
    color: #766357;
  }
}
.main-promo {
  background-image: url("../img/triangle-mobile.svg"), url("../img/back-mobile@1.jpg");
  background-size: 100% auto, cover;
  background-position: left 0 bottom 0;
  background-color: #81b3d2;
  background-repeat: no-repeat, no-repeat;
  padding: 90px 0 85px 0;
  height: 389px;
}
@media (min-resolution: 2dppx) {
  .main-promo {
    background-image: url("../img/triangle-mobile.svg"), url("../img/back-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .main-promo {
    background-image: url("../img/triangle-tablet.svg"), url("../img/back-tablet.jpg");
    height: 518px;
    padding: 77px 0 91px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .main-promo {
    background-image: url("../img/triangle-tablet.svg"), url("../img/back-tablet@2x.jpg");
  }
}
@media (min-width: 1200px) {
  .main-promo {
    background-image: url("../img/triangle-desktop.svg"), url("../img/back-desktop@1.jpg");
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .main-promo {
    background-image: url("../img/triangle-desktop.svg"), url("../img/back-desktop@2x.jpg");
  }
}
.main-promo__image {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 768px) {
  .main-promo__image {
    width: 461px;
  }
}
@media (min-width: 1200px) {
  .main-promo__image {
    width: 459px;
  }
}
.main-footer__hashtag {
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  color: #000000;
  margin-bottom: 30px;
}
.main-footer__hashtag:hover,
.main-footer__hashtag:focus-within {
  color: #81b3d2;
}
.main-footer__hashtag:active {
  color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 768px) {
  .main-footer__hashtag {
    margin: 0;
  }
}
.main-footer__wrapper {
  display: flex;
  flex-direction: column;
  padding: 36px 18px;
  align-items: center;
  text-align: center;
}
@media (min-width: 768px) {
  .main-footer__wrapper {
    flex-direction: row;
    padding: 36px 0;
    margin: 0 60px;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
  }
}
@media (min-width: 1200px) {
  .main-footer__wrapper {
    margin: 0 120px;
  }
}
.main-footer__list {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 0;
  gap: 6px;
}
.main-footer__copyright-link {
  display: flex;
}
.main-footer__copyright-link:hover,
.main-footer__copyright-link:focus-within {
  color: #81b3d2;
}
.main-footer__copyright-link:active {
  color: #b3b3b3;
}
.main-footer__copyright-text {
  font-weight: 400;
  margin: 0;
  align-self: center;
  margin-right: 12px;
}
.page-promo {
  background-image: url("../img/triangle-mobile.svg"), url("../img/back-mobile@1.jpg");
  background-size: 100% auto, cover;
  background-position: left 0 bottom 0;
  background-color: #81b3d2;
  background-repeat: no-repeat, no-repeat;
  padding: 52px 45px 67px;
}
@media (min-resolution: 2dppx) {
  .page-promo {
    background-image: url("../img/triangle-mobile.svg"), url("../img/back-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .page-promo {
    background-image: url("../img/triangle-tablet.svg"), url("../img/back-tablet.jpg");
    padding: 95px 155px 117px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .page-promo {
    background-image: url("../img/triangle-tablet.svg"), url("../img/back-tablet@2x.jpg");
  }
}
@media (min-width: 1200px) {
  .page-promo {
    background-image: url("../img/triangle-desktop.svg"), url("../img/back-desktop@1.jpg");
    padding: 101px 370px 120px;
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .page-promo {
    background-image: url("../img/triangle-desktop.svg"), url("../img/back-desktop@2x.jpg");
  }
}
.page-promo__image {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 768px) {
  .page-promo__image {
    width: 457px;
  }
}
@media (min-width: 1200px) {
  .page-promo__image {
    width: 460px;
  }
}
.field-control {
  padding: 0 20px;
  margin-top: 14px;
}
@media (min-width: 768px) {
  .field-control {
    padding: 0;
    margin-top: 10px;
  }
  .field-control:nth-child(3) {
    margin-top: 16px;
  }
}
.field-control__label {
  margin-left: 16px;
}
.field-control__input {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #000000;
  background-color: #f2f2f2;
  padding: 12px;
  border: 4px solid transparent;
  width: 100%;
}
.field-control__input::placeholder {
  color: rgba(0, 0, 0, 0.3);
}
.field-control__input:focus {
  outline: 0;
  outline-offset: 0;
  border: 4px solid #e5e5e5;
  background-color: #ffffff;
}
.field-control__input:hover:not(:focus) {
  background-color: #e5e5e5;
}
@media (min-width: 768px) {
  .field-control__input {
    min-width: 295px;
  }
}
@media (min-width: 1200px) {
  .field-control__input {
    width: 430px;
  }
}
.map iframe {
  width: 100%;
  background-color: #81b3d2;
  border: none;
  display: block;
  height: 350px;
  background-size: 100% auto, cover;
  background-position: center center;
  background-image: url("../img/map-mobile.jpg");
}
@media (min-resolution: 2dppx) {
  .map iframe {
    background-image: url("../img/map-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .map iframe {
    background-image: url("../img/map-tablet.jpg");
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .map iframe {
    background-image: url("../img/map-tablet@2x.jpg");
  }
}
@media (min-width: 1200px) {
  .map iframe {
    background-image: url("../img/map-desktop.jpg");
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .map iframe {
    background-image: url("../img/map-desktop@2x.jpg");
  }
}
.page-wrapper {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  min-width: 320px;
  max-width: 1200px;
  min-height: 100%;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.2);
}
.social {
  margin: 0;
  margin-bottom: 30px;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  gap: 6px;
  padding: 0;
  min-width: 156px;
}
@media (min-width: 768px) {
  .social {
    margin: 0;
  }
}
.social__item {
  background-color: #81b3d2;
}
.social__link {
  display: block;
  margin: 0;
  width: 48px;
  height: 48px;
}
.social__link:hover,
.social__link:focus-within {
  background-color: #669ec0;
}
.social__link:active {
  background-color: #5496bd;
}
.social__icon {
  margin: 12px;
  fill: #ffffff;
}
.social__icon:active {
  opacity: 0.3;
}
.advantages {
  padding: 0;
  position: relative;
}
.advantages__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 49px;
  padding-top: 24px;
  position: relative;
}
@media (min-width: 320px) and (max-width: 768px) {
  .advantages__title br {
    display: none;
  }
}
@media (min-width: 768px) {
  .advantages__title {
    font-size: 21px;
    padding: 0 20px;
    margin-bottom: 56px;
  }
}
@media (min-width: 1200px) {
  .advantages__title {
    padding: 0 50px;
    margin-bottom: 58px;
  }
}
.advantages__description {
  margin: 0;
}
@media (max-width: 320px) {
  .advantages__description br {
    display: none;
  }
}
@media (min-width: 768px) {
  .advantages__description {
    padding: 0;
  }
}
@media (min-width: 1200px) {
  .advantages__description br {
    display: none;
  }
}
.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
  counter-reset: number 0;
  position: relative;
}
@media (max-width: 320px) {
  .advantages__list::before {
    content: url("../img/triangle-section.svg");
    position: absolute;
    top: -14px;
  }
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(4, auto);
  }
}
.advantages__item-img,
.advantages__item {
  counter-increment: number 1;
}
.advantages__item-img .advantages__description,
.advantages__item .advantages__description {
  position: relative;
}
.advantages__item-img .advantages__description::before,
.advantages__item .advantages__description::before {
  position: absolute;
  top: -38px;
  left: 50%;
  transform: translateX(-50%);
  content: "— №" counter(number) " —";
}
@media (min-width: 768px) {
  .advantages__item-img .advantages__description::before,
  .advantages__item .advantages__description::before {
    top: -44px;
  }
}
@media (min-width: 320px) and (max-width: 767px) {
  .advantages__item-img:first-child .advantages__item-photo {
    padding: 30px 20px 40px;
  }
}
@media (min-width: 768px) {
  .advantages__item-img {
    display: grid;
    padding: 0;
    grid-column: 1/-1;
    grid-template-columns: repeat(3, 1fr);
  }
  .advantages__item-img picture {
    grid-column: 2/-1;
  }
  .advantages__item-img--reverse picture {
    grid-column: 1 / span 2;
    grid-row: 1/2;
  }
}
@media (min-width: 1200px) {
  .advantages__item-img:nth-child(2) .advantages__description {
    padding: 0 24px;
  }
}
.advantages__item-photo--blue {
  color: #ffffff;
  background-color: #81b3d2;
  padding: 52px 33px 3px;
}
.advantages__item {
  text-align: center;
  background-color: #eeeeee;
  padding: 0 36px 25px;
}
.advantages__item:last-child .advantages__description {
  padding-bottom: 4px;
}
@media (min-width: 320px) and (max-width: 767px) {
  .advantages__item + .advantages__item .advantages__title {
    border-top: 1px solid #d6d6d6;
  }
}
@media (min-width: 768px) {
  .advantages__item {
    padding: 44px 32px 41px;
  }
}
@media (min-width: 1200px) {
  .advantages__item {
    padding: 52px 32px 48px;
  }
  .advantages__item .advantages__title {
    padding: 0 90px;
  }
  .advantages__item .advantages__description {
    padding: 0 10px;
  }
  .advantages__item:nth-child(4) .advantages__description {
    padding: 0 34px;
  }
}
.advantages__item-wrapper {
  padding: 24px;
}
.advantages__item-photo {
  padding: 13px 18px 37px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .advantages__item-photo {
    padding: 52px 33px 31px;
  }
}
@media (min-width: 1200px) {
  .advantages__item-photo {
    padding: 52px 60px 51px;
  }
}
@media (max-width: 767px) {
  .advantages__image {
    display: block;
    width: 100%;
  }
}
@media (min-width: 768px) {
  .advantages__image {
    grid-column: 2/-1;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}
@media (min-width: 768px) {
  .advantages__item-photo--reverse {
    grid-column: 3/-1;
  }
}
@media (min-width: 768px) {
  .advantages__image--reverse {
    grid-column: 1 / span 2;
    grid-row: 1/2;
  }
}
@media (min-width: 768px) {
  .advantages__service {
    display: flex;
    grid-column: 1 / 4;
  }
}
.services {
  margin: 0;
  padding: 24px 18px 18px 0;
  text-align: left;
  list-style: none;
}
@media (min-width: 768px) {
  .services {
    padding: 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(4, auto);
  }
}
.services__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #000000;
  padding: 8px 0 16px;
  margin: 0;
}
@media (min-width: 768px) {
  .services__title {
    font-size: 21px;
    padding-bottom: 22px;
  }
}
.services__description {
  margin: 0;
}
@media (max-width: 768px) {
  .services__description br {
    display: none;
  }
}
.services__item {
  margin: 0;
  margin-bottom: 26px;
  padding: 0;
  padding-left: 65px;
  position: relative;
}
.services__item::after {
  content: "";
  position: absolute;
  width: 40px;
  height: 40px;
  top: 0;
  left: 20px;
  background-repeat: no-repeat;
  background-size: contain;
}
.services__item--residence::after {
  background-image: url("../img/house.svg");
}
.services__item--food::after {
  background-image: url("../img/burger.svg");
}
.services__item--souvenirs::after {
  background-image: url("../img/present.svg");
}
@media (min-width: 768px) {
  .services__item {
    text-align: center;
    min-width: 256px;
    box-sizing: border-box;
    padding: 150px 44px 44px;
    margin: 0;
  }
  .services__item::after {
    width: 80px;
    height: 80px;
    top: 54px;
    left: 50%;
    transform: translateX(-50%);
  }
  .services__item--souvenirs .services__title {
    padding-top: 4px;
  }
  .services__item--souvenirs::after {
    top: 60px;
  }
  .services__item--food .services__description {
    padding: 0 6px;
  }
}
@media (min-width: 1200px) {
  .services__item {
    padding: 162px 50px 70px;
  }
  .services__item::after {
    top: 64px;
  }
  .services__item--souvenirs .services__title {
    padding-top: 8px;
  }
  .services__item--food .services__title {
    padding-top: 4px;
    padding-bottom: 24px;
  }
}
.search {
  text-align: center;
  padding-bottom: 6px;
}
@media (min-width: 768px) {
  .search {
    padding: 0;
  }
}
.search__wrapper {
  padding: 38px 18px 49px 18px;
}
@media (min-width: 768px) {
  .search__wrapper {
    max-width: 458px;
    padding: 60px 0 55px;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .search__wrapper {
    padding-bottom: 66px;
  }
}
.search__title {
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
